@import url('https://fonts.googleapis.com/css2?family=Arvo:ital,wght@0,400;0,700;1,400;1,700&family=Chakra+Petch:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');

:root {
	--background: #000000;
	--text: #fefefe;
	// --main-color: #5ad5a8;
	--main-color: #fefefe;
	--small: 7px;
	--medium: 14px;
	--large: 28px;
}

::-webkit-scrollbar {
	width: 0;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	text-decoration: none;
	font-family: "Arvo", serif;
	z-index: 10;
	list-style: none;
}

h1, h2, h3, h4, h5 {
	font-family: "Chakra Petch", serif;
}

p {
	margin-bottom: var(--small);
}

.txc {
	text-align: center;
}

html {
	background-color: var(--background);
	color: var(--text);
	font-size: 17px;
}

input, button, .btn {
	color: var(--main-color);
	padding: 7px 14px;
	border: 2px solid var(--main-color);
}

.mt-m {
	margin-top: var(--medium);
}

.mb-l {
	margin-bottom: var(--large);
}